    method: "POST",
    data: form,
  });
  return res.data["done"] as boolean;
};

const connect = async () => {
  const load = await showLoading("Connexion...");
  if (!(await has_account())) {
    load.dismiss();
    return show_alert(
      "Connexion impossible",
      "Aucun compte vendeur n'est associé à ces identifiants"
    );
  }
